
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
components: {
XHeader
},
data() {
return {
title: "title",
isLoading: false,
username: "",
password: ""
};
},
methods: {
loginIn() {
if (this.username != "" || this.password != "") {
this.isLoading = true;
sessionStorage.setItem("userName", this.username);
sessionStorage.setItem("userToken", "iigslfdjzgertuq589e6uhgf"); 
this.$store.commit("setUserName", this.username);
this.$store.commit("setUserToken", "iigslfdjzgertuq589e6uhgf");

this.$router.push({
name: "home-page"
});
}
}
},
watch: {
$route(to, from) {
if (to.path != "/") {
this.title = to.meta;

} else {
}
}
}
};



